import React, { Component } from 'react';
import './App.css';
import Grid from '@material-ui/core/Grid';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Doctor from './assets/Doctors1.png';
import DoctorNext from './assets/Doctors3.png';
import Tabletop from 'tabletop';
import Emotions from './emotionsChart';

class App extends Component {
	constructor() {
		super();
		this.state = {
			conditionsFlag: false,
			conditionSelected: '',
			conditionsData: [],
			uniqueConditions: [],
			emotionsData: [],
			drug: '',
			drugSelected: [],
			drugFlag: false,
		};
	}

	handleConditionSubmit = (event) => {
		if (this.state.condition !== '') {
		}
	};

	handleConditionChange = (event) => {
		this.setState({ conditionSelected: event.target.value });
	};

	handleConditionClear = (event) => {
		this.setState({ conditionSelected: '', conditionsFlag: false });
	};

	handleDrugChange = (event) => {
		this.setState({
			drug: event.target.value,
			drugSelected: [],
			drugFlag: false,
		});
	};

	handleDrugSubmit = (event) => {
		if (this.state.drug !== '') {
			const tempDrug = this.state.emotionsData.filter((drug) =>
				drug.DrugName.includes(this.state.drug)
			);
			this.setState({
				drugSelected: tempDrug,
				drugFlag: true,
			});
		}
	};

	handleDrugClear = (event) => {
		this.setState({ drug: '', drugSelected: [], drugFlag: false });
	};

	componentDidMount() {
		Tabletop.init({
			key: '1x6wMW_1i66aLaWAfwLpYd5PDyujEShyHt7bIXU5b4fQ',
			simpleSheet: true,
		})
			.then((data, tabletop) => {
				this.setState({
					conditionsData: data,
				});
			})
			.then(() => {
				var lookup = {};
				var result = [];
				for (var item, i = 0; (item = this.state.conditionsData[i++]); ) {
					var condition = item.condition;
					if (!(condition in lookup)) {
						lookup[condition] = 1;
						result.push(condition);
					}
				}
				this.setState({ uniqueConditions: result });
			});

		Tabletop.init({
			key: '1y0tJFI-PgyzZMnH96qzaSmjFp-6aU_Co2NeIT5w4Q9o',
			simpleSheet: true,
		}).then((data, tabletop) => {
			this.setState({
				emotionsData: data,
			});
		});
	}

	render() {
		return (
			<div className="App">
				<h1 className="heading">
					Pill Recommendation System{' '}
					<span role="img" aria-label="wink">
						ðŸ’Š
					</span>
				</h1>
				<div className="content">
					<Grid
						container
						direction="row"
						justify="center"
						alignItems="center"
						spacing={8}
						style={{ width: '100%', height: '100%', margin: 'auto' }}
					>
						<Grid item xs={12} sm={12} md={6} lg={6} style={{ margin: 'auto' }}>
							{!this.state.drugFlag ? (
								<img
									src={DoctorNext}
									className="DoctorImageNext"
									alt="Stay Safe ;)"
								/>
							) : (
								<Emotions drugData={this.state.drugSelected} />
							)}
						</Grid>
						<Grid item xs={12} sm={12} md={6} lg={6} style={{ margin: 'auto' }}>
							<div className="form">
								<p className="description">
									Check emotions associated with a particular drug
								</p>
								<Select
									displayEmpty
									style={{ width: '50%' }}
									value={this.state.drug}
									onChange={this.handleDrugChange}
								>
									<MenuItem value="" disabled>
										Drug
									</MenuItem>
									{this.state.emotionsData.map((drug) => {
										return (
											<MenuItem key={drug['DrugName']} value={drug['DrugName']}>
												{drug['DrugName']}
											</MenuItem>
										);
									})}
								</Select>
								<div className="buttons">
									<button id="submitButton" onClick={this.handleDrugSubmit}>
										Search
									</button>
									<button id="clearButton" onClick={this.handleDrugClear}>
										Clear
									</button>
								</div>
							</div>
						</Grid>
					</Grid>
					<Grid
						container
						direction="row"
						justify="center"
						alignItems="center"
						spacing={8}
						style={{ width: '100%', height: '95vh', margin: 'auto' }}
					>
						<Grid item xs={12} sm={12} md={6} lg={6} style={{ margin: 'auto' }}>
							<div className="form">
								<p className="description">
									Analyzing the drugs descriptions, conditions, reviews and then
									recommending it using Deep Learning Models, for each health
									condition of a patient
								</p>
								<Select
									displayEmpty
									style={{ width: '50%' }}
									value={this.state.conditionSelected}
									onChange={this.handleConditionChange}
								>
									<MenuItem value="" disabled>
										Condition
									</MenuItem>
									{this.state.uniqueConditions.map((condition) => {
										return (
											<MenuItem key={condition} value={condition}>
												{condition}
											</MenuItem>
										);
									})}
								</Select>
								<div className="buttons">
									<button
										id="submitButton"
										onClick={this.handleConditionSubmit}
									>
										Search
									</button>
									<button id="clearButton" onClick={this.handleConditionClear}>
										Clear
									</button>
								</div>
							</div>
						</Grid>
						<Grid item xs={12} sm={12} md={6} lg={6} style={{ margin: 'auto' }}>
							{!this.state.conditionsFlag ? (
								<img src={Doctor} className="DoctorImage" alt="Stay Safe ;)" />
							) : (
								<h1>Data</h1>
							)}
						</Grid>
					</Grid>
					<div style={{ width: '100%', height: '5vh', margin: 'auto' }}>
						<p className="footer">
							Made with {'<3'} by{' '}
							<a
								href="mailto:goyal.16@iitj.ac.in"
								target="_blank"
								rel="noreferrer"
							>
								Aditi,
							</a>{' '}
							<a
								href="mailto:jain.38@iitj.ac.in"
								target="_blank"
								rel="noreferrer"
							>
								Darshit,
							</a>{' '}
							and{' '}
							<a
								href="mailto:agarwal.10@iitj.ac.in"
								target="_blank"
								rel="noreferrer"
							>
								Harsh
							</a>{' '}
						</p>
					</div>
				</div>
			</div>
		);
	}
}

export default App;
